* {
  box-sizing: border-box;
}

html, body{
  height: 100%;
}

body{
  margin: 0;
  padding: 0;
  font-family: Helvetica;
  background: linear-gradient(180deg, rgba(9, 35, 175, 1) 0%, rgba(0, 0, 0, 1) 100%);
  color: #fff;
}

button{
  background: #1646a0;
  color: #fff;
  padding: 0 45px;
  border: 2px solid #fff;
  border-radius: 25px;
  height: 50px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.2em;
  cursor: pointer;
  transition: background .4s ease;
}

button:hover{
  background: #0923af;
}